import { useState } from 'react'

import { useGetAmmPools, useGetAmmPoolsBalances } from 'hooks/exchange/useAmmpoolAPI'
import { useCustomDCEffect } from '@loopring-web/sdk/hooks/useCustomDCEffect'
import { useGetTokens } from 'hooks/exchange/useExchangeAPI'

import { getAmmInfo, } from 'utils/swap_calc_utils'

export function useSwapPage() {

    const [base, setBase] = useState<string>('')
  
    const [quote, setQuote] = useState<string>('')
  
    const { ammpools } = useGetAmmPools()

    const { ammPoolsBalances: ammPoolsBalances_all } = useGetAmmPoolsBalances()
  
    const ammPoolsBalances = ammPoolsBalances_all?.ammpoolsbalances

    const [poolAddress, setPoolAddress] = useState<string>('')
  
    const [market, setMarket] = useState<string>('')
  
    const { tokens: tokensAll } = useGetTokens()
  
    const tokens = tokensAll?.tokenSymbolMap

    useCustomDCEffect(() => {
  
      if (!base || !quote || !ammpools || !ammPoolsBalances) {
        return
      }
  
      const { ammInfo, } = getAmmInfo(ammpools.ammpools, ammPoolsBalances, base, quote)
  
      setMarket(ammInfo.market)
      setPoolAddress(ammInfo.address)
  
    }, [base, quote, ammpools, ammPoolsBalances,])

    return {
        base,
        setBase,
        quote,
        setQuote,
        market,
        tokens,
        poolAddress,
        ammpools,
        ammPoolsBalances,
    }

}