import { useState } from 'react'

import { useAmmpoolAPI, useUserAPI } from '@loopring-web/sdk/hooks/common/useApi'
import { useAccount } from 'stores/account/hook'

import { dumpError400 } from '@loopring-web/sdk/utils/network_tools'

import { useCustomDCEffect } from '@loopring-web/sdk/hooks/useCustomDCEffect'
import { AmmTrade, FilledType } from '@loopring-web/sdk/defs/loopring_defs'

export function useGetMyTrades(market: string) {

  const { apiKey, accountId, } = useAccount()

  const userApi = useUserAPI()

  const [myTrades, setMyTrades] = useState<any>()

  useCustomDCEffect(() => {
    if (!userApi || !accountId || !apiKey || !market) {
      return
    }

    userApi.getUserTrades({
      accountId,
      market,
      fillTypes: FilledType.amm,
    }, apiKey).then((trades) => {

      let tradesNew: any = []
      trades.trades.forEach((item: any) => {
        tradesNew.push([item[2], item[3], item[4], item[6], item[0]])
      })
      
      setMyTrades(tradesNew)
    }).catch((reason) => {
      dumpError400(reason)
    })

  }, [userApi, accountId, apiKey, market])

  return {
    myTrades,
  }
}

export function useGetRecentTrades(ammPoolAddress: string) {

  const ammPoolApi = useAmmpoolAPI()

  const [recentTrades, setRecentTrades] = useState<any>()

  useCustomDCEffect(() => {
    if (!ammPoolApi || !ammPoolAddress) {
      return
    }

    ammPoolApi.getAmmPoolTrades({
      ammPoolAddress
    }).then((trades) => {
      let tradesNew: any = []
      trades.trades.forEach((item: AmmTrade) => {
        tradesNew.push([item.side, item.price, item.feeAmount, item.createdAt])
      })
      // console.log('getAmmPoolTrades:', tradesNew)
      setRecentTrades(tradesNew)
    }).catch((reason) => {
      dumpError400(reason)
    })

  }, [ammPoolApi, ammPoolAddress])

  return {
    recentTrades,
  }
}
