export function useCoinMap(exchangeApi: ExchangeAPI, tokens: any) {

    const [coinMap, setCoinMap] = useState<CoinMap<any, CoinInfo<any>>>({})

    useCustomDCEffect(async () => {

        if (!exchangeApi || !tokens) {
            return
        }

        const keys = Reflect.ownKeys(tokens)

        let coinMap: CoinMap<any, CoinInfo<any>> = {}

        keys.forEach((key: any) => {

            if (!(key as string).startsWith('LP-')) {
                coinMap[key] = {
                    icon: getIcon(key, tokens),
                    name: key,
                    simpleName: key,
                    description: '',
                    company: '',
                }
            }

        })

        setCoinMap(coinMap)

    }, [exchangeApi, tokens])

    return {
        coinMap,
    }

}

export function useLv1WalletMap(exchangeApi: ExchangeAPI, account: Lv2Account, tokens: any) {

    const [walletMap, setWalletMap] = useState<WalletMap<any, WalletCoin<any>>>({})

    useCustomDCEffect(async () => {

        if (!exchangeApi || !account || !account.accAddr || !tokens) {
            return
        }

        let walletMap: WalletMap<any, WalletCoin<any>> = {}

        const { tokenBalances } = await exchangeApi.getTokenBalances({
            owner: account.accAddr, token: ''
        }, tokens)

        const keys = Reflect.ownKeys(tokenBalances)

        keys.forEach((item: any) => {
            const balance = tokenBalances[item]
            walletMap[item] = {
                belong: item,
                count: fromWEI(tokens, item, balance) as number,
            }
        })

        setWalletMap(walletMap)

    }, [exchangeApi, account, tokens])

    return {
        walletMap,
    }

}

export function useLv2WalletMap(tokens: any) {

    const { balances } = useGetUserBalances(tokens)

    const [walletMap, setWalletMap] = useState<WalletMap<any, WalletCoin<any>>>({})

    useCustomDCEffect(() => {
        let walletMap: WalletMap<any, WalletCoin<any>> = {}
        if (balances && balances.symbol) {
            const keys = Reflect.ownKeys(balances.symbol)

            keys.forEach((item: any) => {
                const balance = balances.symbol[item]
                walletMap[item] = {
                    belong: item,
                    count: fromWEI(tokens, item, balance) as number,
                }
            })
        }

        console.log('lv2:', walletMap)

        setWalletMap(walletMap)
    }, [balances, tokens])

    return {
        walletMap,
    }

}

export function useChargeFeeList(tokenSymbol: string | undefined, requestType: OffchainFeeReqType) {

    const api = useUserAPI()

    const { accountId, apiKey, } = useAccount()

    const [chargeFeeList, setChargeFeeList] = useState<any>([])

    useCustomDCEffect(async () => {

        console.log('!!!!!!!!!useCustomDCEffect useChargeFeeList:', tokenSymbol, accountId)

        if (!accountId || !tokenSymbol) {
            return
        }

        let chargeFeeList: any[] = []

        try {

            const request: GetOffchainFeeAmtRequest = {
                accountId,
                tokenSymbol,
                requestType,
            }

            const response = await api.getOffchainFeeAmt(request, apiKey)

            if (response) {
                response.fees.forEach((item: any, index: number) => {
                    chargeFeeList.push({ belong: item.token, fee: item.fee })
                })

                setChargeFeeList(chargeFeeList)
            }
            console.log('response:', response)

        } catch (reason) {
            dumpError400(reason)
        }


        setChargeFeeList(chargeFeeList)

    }, [accountId, apiKey, tokenSymbol])

    return {
        chargeFeeList,
    }

}

