import { useRouteMatch } from 'react-router'

import { Box, Paper } from '@material-ui/core'
import {
    SubMenu,
    SubMenuList as BasicSubMenuList,
} from '@loopring-web/component-lib'
import { withTranslation } from 'react-i18next'
import styled from '@emotion/styled'
import {
    AmmIcon, AssetsIcon, OrderIcon, TradeIcon, TransactionsIcon
} from '@loopring-web/component-lib/src/static-resource'

import TxPanel from './TxPanel'
import AssetPanel from './AssetPanel'
import TradePanel from './TradePanel'
import OrderPanel from './OrderPanel'

export const subMenu = {
    assetsGroup: [{
        icon: AssetsIcon,
        router: { path: '/layer2/assets' },
        label: {
            id: 'assets',
        },
    }],
    transactionsGroup: [{
        icon: TransactionsIcon,
        router: { path: '/layer2/transactions' },
        label: {
            id: 'transactions',
        },
    },
    {
        icon: TradeIcon,
        router: { path: '/layer2/trades' },
        label: {
            id: 'trades',
        },
    }
        /*, {
            icon: AmmIcon,
            router: {path: ''},
            label: {
                id: 'ammRecords',
                description: 'ammRecordsDes',
            },
        }*/

    ],
    countInfoGroup: [{
        icon: OrderIcon,
        router: { path: '/layer2/orders' },
        label: {
            id: 'orders',
        },
    }
    ]
}

const StylePaper = styled(Box)`
      width: 100%;
      height: 100%;
      flex: 1;
      background-color: ${({ theme }) => theme.colorBase.background().default};
      border-radius: ${({ theme }) => theme.unit}px;
    ` as typeof Paper

const SubMenuList = withTranslation('layout', { withRef: true })(BasicSubMenuList);
export const Layer2Page = () => {

    let match: any = useRouteMatch("/layer2/:item")

    const selected = match?.params.item ?? 'assets'

    console.log('match:', match, ' selected:', selected, ' item', match?.params.item)

    return (
        <>
            <Box width={'200px'} display={'flex'} justifyContent={'stretch'}>
                <SubMenu>
                    <SubMenuList selected={selected} subMenu={subMenu as any} />
                </SubMenu>
            </Box>
            <Box minHeight={420} display={'flex'} alignItems={'stretch'} flexDirection="row" marginTop={0} flex={1}>

                <Box flex={1} marginLeft={3} display={'flex'} flexDirection={'column'} >
                    {selected === 'assets' && <AssetPanel />}
                    {selected === 'transactions' && <TxPanel />}
                    {selected === 'trades' && <TradePanel />}
                    {selected === 'orders' && <OrderPanel />}
                </Box>
            </Box>
        </>
    )

}
