import { useExchangeAPI } from '@loopring-web/sdk/hooks/common/useApi'
import { useSelector, useDispatch } from 'react-redux'
import { useCustomDCEffect } from '@loopring-web/sdk/hooks/useCustomDCEffect'

// import { useAuthNoKeyApiInstance, useAuthWsApiInstance, } from '@loopring-web/sdk/hooks/common/useApi'

import { RootState } from 'stores'

import { setMarkets, setExchangeInfo, setTokens, } from 'stores/trading/reducer'

export function useGetTradingInfo() {
    const tradingInfo = useSelector((state: RootState) => state.trading)
    return {
        tradingInfo,
        exchangeInfo: tradingInfo.exchangeInfo,
        /*
        markets: tradingInfo.markets,
        tokens: tradingInfo.tokens,
        */
    }
}

export function useUpdateExchangeMarkets() {
    const dispatch = useDispatch()
    const api = useExchangeAPI()

    useCustomDCEffect(() => {

        if (!api) {
            return
        }

        api.getMarkets().then((response: any) => {
    
            dispatch(setMarkets(response))
    
        }, (reject: any) => {
            console.log(reject)
        })

    }, [api])

}

export function useUpdateExchangeInfo() {

    const dispatch = useDispatch()
    const api = useExchangeAPI()

    useCustomDCEffect(() => {

        if (!api) {
            return
        }

        api.getExchangeInfo().then((response: any) => {
            dispatch(setExchangeInfo(response.exchangeInfo))
        }, (reject: any) => {
            console.log(reject)
        })

    }, [api])

}

export function useUpdateTokens() {

    const dispatch = useDispatch()
    const api = useExchangeAPI()

    useCustomDCEffect(() => {

        if (!api) {
            return
        }

        api.getTokens().then((response: any) => {
            console.log('useUpdateTokens response:', response)
            dispatch(setTokens(response.tokenSymbolMap))
        }, (reject: any) => {
            console.log(reject)
        })

    }, [api])

}
