import React, { useCallback } from 'react'

import { useTheme } from '@emotion/react'

import { useTranslation } from 'react-i18next'

import {
    gatewayList as DefaultGatewayList,
    headerMenuData, HeaderMenuTabStatus,
    headerToolBarData, HeadMenuTabKey,
    ButtonComponentsMap,
    WalletStatus,
    GatewayItem, NavListIndex, HideIcon, ViewIcon,
} from '@loopring-web/component-lib/src/static-resource'

import { useAccount, useDisconnect, useUnlock, useConnect, } from 'stores/account/hook'

import { getShortAddr } from 'utils/web3_tools'

import { AccountStatus } from 'state_machine/account_machine_spec'

import { useCustomDCEffect } from '@loopring-web/sdk/hooks/useCustomDCEffect'

import { Theme, } from 'defs/common_defs'

import { Button, useSettings } from '@loopring-web/component-lib'

import store from 'stores'

import { ConnectorNames } from '@loopring-web/sdk/defs/web3_defs'
import { IconButton, Link } from '@material-ui/core';
import { AccountInfoProps } from '@loopring-web/component-lib/components/block';
import { ButtonProps } from '@loopring-web/component-lib/components/basic-lib/index';
import { useEtherscan } from '@loopring-web/sdk/hooks/web3/useWeb3'

export const useHeader = () => {

    const [open, setOpen] = React.useState(false)

    const forceUpdate = React.useReducer((bool) => !bool, false)[1]

    const settings = useSettings()

    const { account } = useAccount()

    const { lock, unlock } = useUnlock()

    const { disconnect } = useDisconnect()

    const { connect } = useConnect()

    const { etherscanUrl } = useEtherscan()

    const gatewayList: GatewayItem[] = [
        {
            ...DefaultGatewayList[0],
            handleSelect: () => {
                console.log('try to connect to ', ConnectorNames.Injected)
                connect(ConnectorNames.Injected, true)
                setOpen(false)
            }
        },
        /*
        {
            key: ConnectorNames.WalletConnect,
            imgSrc: '',
            handleSelect: () => {
                connect(ConnectorNames.WalletConnect)
                setOpen(false)
            }
        },
        {
            key: ConnectorNames.Ledger,
            imgSrc: '',
            handleSelect: () => {
                connect(ConnectorNames.Ledger)
                setOpen(false)
            }
        },
        {
            key: ConnectorNames.Trezor,
            imgSrc: '',
            handleSelect: () => {
                connect(ConnectorNames.Trezor)
                setOpen(false)
            }
        },
        */
    ]

    const [accountInfoProps, setAccountInfoProps] = React.useState<undefined|AccountInfoProps>(undefined);

    const onModeChange = () => {
        console.log('onModeChange')
        if (settings.themeMode === Theme.dark) {
            settings.setThemeWrapper(Theme.light)
        } else {
            settings.setThemeWrapper(Theme.dark)
        }
    }

    const { i18n } = useTranslation()

    const theme: any = useTheme()

    const onNotification = React.useCallback(async () => {
        console.log('onNotification click')
    }, [])

    const onWalletBtnConnent = React.useCallback(async () => {
        console.log('onWalletBtnConnent click')
        const acc = store.getState().account
        switch (acc.status) {
            case AccountStatus.UNCONNNECTED:
                setOpen(true)
                break
            case AccountStatus.LOCKED:
                console.log('onWalletBtnConnent try to unlock')
                await unlock(acc)
                break
            case AccountStatus.ACTIVATED:
                console.log('onWalletBtnConnent try to lock')
                await lock(acc)
                break

            default:
                break
        }
    }, [setOpen, lock, unlock])

    const onThemeBtnClick = React.useCallback(async () => {
        console.log('onThemeBtnClick click')
    }, [])

    const onLangBtnClick = (lang: any) => {
        i18n.changeLanguage(lang)
    }
    const [showAccountInfo, setShowAccountInfo] = React.useState(account?.accAddr ? true : false)
    const [advanceOpen,setAdvanceOpen] = React.useState(false);

    console.log('account111: ', account)
    console.log('showAccountInfo: ', showAccountInfo)

    const handleViewAccountInfo =  React.useCallback((_event: React.MouseEvent) => {
        console.log('handleViewAccountInfo: ', showAccountInfo)
        if (showAccountInfo) {
            // headerMenuData[ NavListIndex.layer2 ].iconBtn.view = false;
            setShowAccountInfo(false);
        } else {
            // headerMenuData[ NavListIndex.layer2 ].iconBtn.view = true;
            setShowAccountInfo(true);
        }
    },[setShowAccountInfo, showAccountInfo])

    const {t}  = useTranslation(['layout']);

    //init componment
    useCustomDCEffect(() => {
        headerToolBarData[ButtonComponentsMap.Download] = {
            ...headerToolBarData[ButtonComponentsMap.Download],
            url:'https://apps.apple.com/us/app/loopring-smart-wallet/id1550921126',
            title:t('labelDownloadAppTitle'),
            description:<Link href='https://apps.apple.com/us/app/loopring-smart-wallet/id1550921126'>{t('labelDownloadBtn')}</Link>
        }

        headerToolBarData[ButtonComponentsMap.WalletConnect] = {
            ...headerToolBarData[ButtonComponentsMap.WalletConnect],
            handleClick: onWalletBtnConnent,
        }

        // headerToolBarData[ButtonComponentsMap.Notification] = { ...headerToolBarData[ButtonComponentsMap.Theme], themeMode: theme.mode, handleClick: onNotification }
        // headerToolBarData.update(ButtonComponentsMap.WalletConnect,value=>{
        //   return {...value, handleClick:onWalletBtnConnent}});
        headerToolBarData[ButtonComponentsMap.Theme] = { ...headerToolBarData[ButtonComponentsMap.Theme], themeMode: theme.mode, handleClick: onThemeBtnClick }
        // headerToolBarData.update(ButtonComponentsMap.Theme,value=>{
        //   return {...value, themeMode:theme.mode, handleClick:onThemeBtnClick}});
        headerToolBarData[ButtonComponentsMap.Language] = { ...headerToolBarData[ButtonComponentsMap.Language], language: i18n.language, handleChange: onLangBtnClick }
        // headerToolBarData.update(ButtonComponentsMap.Language,value=>{
        //  return  {...value, language:i18n.language, handleClick:onThemeBtnClick}});
    }, [theme, i18n, onWalletBtnConnent, onThemeBtnClick, onLangBtnClick, onNotification, t]);

    useCustomDCEffect(() => {

        if (!account) {
            console.log('account'+account+'* exit')
            setAccountInfoProps(undefined)
            return
        }

        const { status } = account

        const addr = getShortAddr(account?.accAddr)

        console.log('account:*', account, '*')
        console.log('addr:*', addr, '*')
        if((addr && accountInfoProps && accountInfoProps.address !== addr) || addr ){
            //TODO getAccount detail info
            setAccountInfoProps({
                address: addr,
                level: 'VIP 1',
                ethLink: etherscanUrl + account?.accAddr,
                //TODO: changed by account status
                mainBtn: <Button variant={'contained'} size={'small'} color={'primary'} onClick={e => setAdvanceOpen(true)}>Advance Setting</Button>
            })
        }else if(addr) {
            setAccountInfoProps(undefined)
        }

        const updateHeaderMenuWhenHasAccountInfo =  ()=>{
            headerMenuData[HeadMenuTabKey.Layer2] = {
                ...headerMenuData[HeadMenuTabKey.Layer2],
                extender : <IconButton disabled={!account}
                                       onClick={handleViewAccountInfo}
                                       aria-label={t('labelShowAccountInfo')} color="primary">
                    {showAccountInfo ? <HideIcon/> : <ViewIcon/>}</IconButton>,
                status: HeaderMenuTabStatus.default
            }
            // setShowAccountInfo(true);
        }
        switch (status) {
            case AccountStatus.UNCONNNECTED:
                headerToolBarData[ButtonComponentsMap.WalletConnect] = {
                    ...headerToolBarData[ButtonComponentsMap.WalletConnect],
                    label: 'Connect Wallet',
                    status: WalletStatus.default
                }
                headerMenuData[HeadMenuTabKey.Layer2] = {
                    //TODO:  HeaderMenuTabStatus.hidden
                    ...headerMenuData[HeadMenuTabKey.Layer2], status: HeaderMenuTabStatus.hidden
                }
                break
            case AccountStatus.LOCKED:
                headerToolBarData[ButtonComponentsMap.WalletConnect] = {
                    ...headerToolBarData[ButtonComponentsMap.WalletConnect],
                    label: addr,
                    status: WalletStatus.connect
                };

                updateHeaderMenuWhenHasAccountInfo();
                break
            case AccountStatus.ACTIVATED:
                headerToolBarData[ButtonComponentsMap.WalletConnect] = {
                    ...headerToolBarData[ButtonComponentsMap.WalletConnect],
                    label: addr,
                    status: WalletStatus.unlock
                }

                updateHeaderMenuWhenHasAccountInfo();
                break
            case AccountStatus.UNACTIVATED:
            case AccountStatus.NOACCOUNT:
                headerToolBarData[ButtonComponentsMap.WalletConnect] = {
                    ...headerToolBarData[ButtonComponentsMap.WalletConnect],
                    // TODO got cache address if no show Connect Wallet
                    label: addr,
                    status: WalletStatus.noAccount
                }

                updateHeaderMenuWhenHasAccountInfo();
                break
            case AccountStatus.DEPOSITING:
            case AccountStatus.DEPOSIT_TO_CONFIREM:
            case AccountStatus.ARPROVING:
            case AccountStatus.APPROV_TO_CONFIRM:
                headerToolBarData[ButtonComponentsMap.WalletConnect] = {
                    ...headerToolBarData[ButtonComponentsMap.WalletConnect],
                    label: addr,
                    status: WalletStatus.accountPending
                }
                updateHeaderMenuWhenHasAccountInfo();

                break
        }
        forceUpdate()
    }, [account, showAccountInfo, etherscanUrl, setAccountInfoProps])

    return {
        headerToolBarData,
        headerMenuData,
        gatewayList,
        open,
        setOpen,
        setShowAccountInfo,
        account,
        showAccountInfo,
        accountInfoProps,
        advanceOpen,
        setAdvanceOpen
    }

}
