import React, { useState } from 'react'

import AppBar from '@material-ui/core/AppBar'
import Tabs from '@material-ui/core/Tabs'
import Tab from '@material-ui/core/Tab'

import {
  Table as TableUI,
  generateRows,
  generateColumns,
  Column,
} from '@loopring-web/component-lib'
import { withTranslation } from 'react-i18next';

interface Row {
  side: string;
  amount: string;
  price: string;
  fee: string;
  time: string;
  sortColumn: string;
  filterColumn: string;
  cellExpend: {
    value: string;
    children: [];
    isExpanded: boolean;
  }
  children?: Row[];
  isExpanded?: boolean;
  format?: any;
}

const columnModeDefault: Column<Row, unknown>[] = [
  { key: 'side', name: 'Side' },
  { key: 'amount', name: 'Amount' },
  { key: 'price', name: 'Price' },
  { key: 'fee', name: 'Fee' },
  { key: 'time', name: 'Time' },
]

const ammColumnModeDefault: Column<Row, unknown>[] = [
  { key: 'side', name: 'Side' },
  { key: 'price', name: 'Price' },
  { key: 'feeAmount', name: 'FeeAmount' },
  { key: 'time', name: 'Time' },
]

const applyProps = (index: number) => {
  return {
    id: `simple-tab-${index}`,
    'aria-controls': `simple-tabpanel-${index}`,
  }
}
const Table = withTranslation('common',{withRef:true})(TableUI);
const TabPanel = (props: any) => {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
      {...other}
    >
      {value === index && (
        <React.Fragment>{children}</React.Fragment>
      )}
    </div>
  )
}

const TradePanel = ({ myTrades, recentTrades }: { myTrades: any, recentTrades: any }) => {

  const [value, setValue] = useState(0)

  const handleChange = (event: any, newValue: any) => {
    setValue(newValue)
  }

  const myArgs: any = {
    rawData: myTrades ? myTrades : [],
    columnMode: columnModeDefault,
    generateRows,
    generateColumns,
  }

  const recentArgs: any = {
    rawData: recentTrades ? recentTrades : [],
    columnMode: ammColumnModeDefault,
    generateRows,
    generateColumns,
  }

  return (
    <>
      {/*<AppBar position="static">*/}
      <Tabs
          value={value}
          onChange={handleChange}
          aria-label="tabs switch"
      >
        <Tab label="My Trades" {...applyProps(0)} />
        <Tab label="Recent Trades" {...applyProps(1)} />
      </Tabs>
      {/*</AppBar>*/}
      <TabPanel value={value} index={0}>
        <Table {...myArgs} />
      </TabPanel>
      <TabPanel value={value} index={1}>
        <Table {...recentArgs} />
      </TabPanel>
    </>
  )

}

export default TradePanel
