import React, { useCallback, useState } from 'react'

import {
    DepositProps,
    Header as HeaderUI,
    HideOnScroll,
    ModalWalletConnect,
    ResetProps,
    setShowDeposit,
    setShowResetAccount,
    setShowTransfer,
    AccountInfo,
    SwapTradeData,
    SwitchData,
    TradeBtnStatus,
    TransferProps,
    WithdrawProps,
} from '@loopring-web/component-lib'

import { headerRoot, WithdrawType, WithdrawTypes } from '@loopring-web/component-lib/src/static-resource'

import { useLocation } from 'react-router-dom'

import { Box, Button, Collapse, Container, Dialog, Toolbar } from '@material-ui/core'

import { useHeader } from './hook'

import { useAccount, useConnect, useDisconnect, useUnlock, } from 'stores/account/hook'
import { useTranslation } from 'react-i18next';
import { useDispatch } from 'react-redux';
import { ModalPanel } from '@loopring-web/component-lib';
import { useGetExchangeInfo } from 'hooks/exchange/useExchangeAPI'
import { SettingPage } from '../../pages/SettingPage';

function TmpDialog(props: any) {

    const { account } = useAccount()

    const { setOpenC, open } = props

    const { lock, unlock } = useUnlock()

    const { disconnect } = useDisconnect()

    const { connect } = useConnect()

    return (
        <Dialog onClose={() => {
            setOpenC(false)
        }} aria-labelledby="simple-dialog-title" open={open}>
            <Button onClick={async () => {
                await lock(account);
                setOpenC(false)
            }}>lock</Button>
            <Button onClick={async () => {
                await unlock(account);
                setOpenC(false)
            }}>unlock</Button>
            <Button>switch</Button>
            <Button onClick={() => {
                disconnect();
                setOpenC(false)
            }}>logout</Button>
            <Button>advanced</Button>
        </Dialog>
    )

}

const Header = ({ ...rest }: any) => {

    const location = useLocation()
    const {
        headerToolBarData,
        headerMenuData,
        gatewayList,
        open,
        setOpen,
        setShowAccountInfo,
        account,
        accountInfoProps,
        showAccountInfo,
        advanceOpen,
        setAdvanceOpen
    } = useHeader()

    const { exchangeInfo } = useGetExchangeInfo()

    const { disconnect } = useDisconnect()

    const onSwitch = useCallback(() => {
        setOpen(true)
    }, [setOpen])



    console.log('-------accountInfoProps:', accountInfoProps, ' showAccountInfo:', showAccountInfo)

    return (<>
        <HideOnScroll>
            {process.env.NODE_ENV !== 'production' && JSON.stringify(account?.status)}
            {process.env.NODE_ENV !== 'production' && ' exchangeAddress:' + JSON.stringify(exchangeInfo?.exchangeAddress)}

            <HeaderUI {...rest} headerMenuData={headerMenuData} headerToolBarData={headerToolBarData}
                      selected={location.pathname === '/' ? headerRoot : location.pathname}></HeaderUI>

        </HideOnScroll>
        <Toolbar />

        <ModalWalletConnect {...{ ...rest, gatewayList, open, onClose: () => setOpen(false) }} />

        {
            accountInfoProps && <><Collapse in={showAccountInfo}>
              <Container maxWidth="lg">
                <Box marginTop={2}>
                  <AccountInfo onDisconnect = { () => { disconnect(); setShowAccountInfo(false); } } onSwith = { onSwitch } {...accountInfoProps}></AccountInfo>
                </Box>
              </Container>
            </Collapse>
              <SettingPage open={advanceOpen} onClose={e=>setAdvanceOpen(false)}/>
            </>
        }

    </>)
}

export default Header
