import TradePanelUi from './TradePanel'
import { useGetMyTrades, useGetRecentTrades, } from './hooks'

const TradePanel = (props: any) => {

  const { market, poolAddress, } = props

  const { myTrades } = useGetMyTrades(market)

  const { recentTrades } = useGetRecentTrades(poolAddress)

  return (
    <TradePanelUi myTrades={myTrades} recentTrades={recentTrades} />
  )

}

export default TradePanel
