import { WithTranslation, withTranslation } from 'react-i18next'
import { PriceTag } from '@loopring-web/component-lib/static-resource'
import { Box, Paper } from '@material-ui/core'
import styled from '@emotion/styled'
import { AssetTitleProps, AssetTitle, AssetsTable, ModalPanel, } from '@loopring-web/component-lib'
import { useModals } from 'hooks/modal/useModals'
import { useModalProps } from './hooks'
import { useGetUserBalances } from 'hooks/exchange/useUserAPI'
import { useGetTokens } from 'hooks/exchange/useExchangeAPI'

const StylePaper = styled(Box)`
  width: 100%;
  height: 100%;
  flex: 1;
  background-color: ${({ theme }) => theme.colorBase.background().default};
  border-radius: ${({ theme }) => theme.unit}px;
  padding: 20px;

  .title {
    font-family: Gilroy-Medium;
    font-size: ${({ theme }) => theme.unit * 3}px;
    line-height: 19px;
  }

  .tableWrapper {
    margin-top: 20px;
    border: 1px solid ${({ theme }) => theme.colorBase.borderColor};
    border-radius: ${({ theme }) => theme.unit}px;
    padding: 26px;
  }
` as typeof Paper;

const StyledChartWrapper = styled(Box)`
    display: flex;
    align-items: center;

    >div {
        flex: 1;
    }
`

const AssetPanel = withTranslation('common')((rest: WithTranslation<'common'>) => {

    const {
        depositProps,
        withdrawProps,
        transferProps,
        resetProps,
    } = useModalProps()

    const {
        ShowDeposit,
        ShowTransfer,
        ShowWithdraw,
        ShowResetAccount,
    } = useModals()

    const onShowDeposit = () => {
        ShowDeposit(true)
    }

    const onShowTransfer = () => {
        ShowTransfer(true)
    }

    const onShowWithdraw = () => {
        ShowWithdraw(true)
    }

    const AssetTitleProps: AssetTitleProps = {
        assetInfo: {
            totalAsset: 123456.789,
            priceTag: PriceTag.Dollar,
        },
        onShowDeposit,
        onShowTransfer,
        onShowWithdraw,
    }

    const { tokens: tokensAll } = useGetTokens()

    const tokens = tokensAll?.tokenSymbolMap

    const { balances } = useGetUserBalances(tokens)

    console.log('balances:', balances)

    return (
        <>
            <Box marginBottom={3}>
                <AssetTitle  {...{
                    ...rest,
                    ...AssetTitleProps
                }} />
            </Box>
            <StylePaper>
                <div className="title">Assets</div>
                {/* <StyledChartWrapper>
                    <div>test</div>
                    <Box>
                        <ScaleAreaChart type="depth" data={[]} />
                    </Box>
                </StyledChartWrapper> */}
                <div className="tableWrapper">
                    <AssetsTable rawData={[]} />
                </div>
            </StylePaper>

            <ModalPanel transferProps={transferProps} withDrawProps={withdrawProps} depositProps={depositProps} resetProps={resetProps}></ModalPanel>

        </>
    )
})

export default AssetPanel
